<template>
    <div>
        <tab-widget :tabs="tabs"></tab-widget>
        <div class="base-table-widget">
            <table class="bt-w-table">
                <thead>
                <tr class="bt-w-title">
                    <td class="bt-w-title-td"></td>
                    <td class="bt-w-title-td pcenter"></td>
                    <td class="bt-w-title-td pcenter"></td>
                    <td class="bt-w-title-td pcenter"></td>
                    <td class="bt-w-title-td pcenter"></td>
                </tr>
                </thead>
                <tbody>
                <tr @click="check_msg" :id="msg.article_id" class="bt-w-tr" v-for="msg of msges">
                    <td class="bt-w-td">{{msg.title}}</td>
                    <td class="bt-w-td pcenter">
                        <head-widget :uname="msg.uname"></head-widget>
                    </td>
                    <td class="bt-w-td pcenter">{{msg.reply}}</td>
                    <td class="bt-w-td pcenter">{{msg.browse}}</td>
                    <td class="bt-w-td pcenter">{{msg.active}}</td>
                </tr>
                </tbody>
            </table>
            <div class="pad-h-3"></div>
            <div class="pcenter">
                <router-link to="/editor?part=discuss_msg_create" class="base-btn-widget"></router-link>
            </div>
        </div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";

    /**/
    var msges = [//data: 
        {
            article_id: "article_1",
            title: "node.js",
            uname: "",
            reply: 4,
            browse: 123,
            active: "2",
        },
        {
            article_id: "article_2",
            title: "trupal 8  url ",
            uname: "",
            reply: 12,
            browse: 783,
            active: "8",
        },
        {
            article_id: "article_3",
            title: " trupal ",
            uname: "",
            reply: 24,
            browse: 5452,
            active: "8",
        },
        {
            article_id: "article_4",
            title: "1025ma.com2",
            uname: "",
            reply: 102,
            browse: 23,
            active: "15",
        },
    ];

    export default {
        name: "student",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "",
                        type: "route",
                        link: "/coffee/teacher",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/coffee/same_class",
                    },
                    {
                        name: "",
                        state: "act",
                        type: "route",
                        link: "/coffee/student",
                    },
                ],

                /**/
                msges: [],

            }
        },
        methods: {

            /**/
            check_msg(e){
                this.$router.push("/blog?article_id=" + e.currentTarget.id);
            }

        },
        components: {
            "tab-widget": Tab,
            "head-widget": Head,
        },
        mounted(){
            this.$axios.get("discuss_msg/")
                .then( res => {
                    let msges = res.data;
                    this.msges = this.$B.data_convert(msges, {
                        article_id: "id",
                        title: "title",
                        uname: (data)=>{
                            return data.user.name;
                        },
                        reply: (data)=>{
                            return data.discuss_replay.length;
                        },
                        browse: "click_num",
                        active: (data)=>{
                            return data.send_time.replace("T", " ");
                        },
                    })
                    this.msges.reverse();
                })
        }
    }
</script>

<style scoped>
    @import "../../assets/css/head-widget.css";
    @import "../../assets/css/chat-record-widget.css";
    @import "../../assets/css/base-table-widget.css";
    @import "../../assets/css/base-btn-widget.css";
</style>


