<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget"></h2>
                    <hr class="seg-widget"/>

                    <!---->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div v-for="race of team_races">
                    <div class="pannel-widget">
                        <h3 class="base-title-widget bt-w-blue">{{race.title}}</h3>
                        <hr class="seg-widget"/>
                        <div class="basic-article-widget pad-w-2">
                            <p class="ba-w-p">{{race.desc}}</p>
                            <p class="ba-w-small-p"><span class="text-strong">:</span> {{race.deadline}}</p>
                            <p class="ba-w-small-p"><span class="text-strong">:</span> {{race.member_limit}} </p>
                            <p class="ba-w-small-p"><span class="text-strong">:</span> {{race.conscription}}</p>
                        </div>
                        <div class="pad-w-2">
                            <div class="pad-h-1"></div>
                            <span :id="race.team_race_id" :state="race.has_joined? 'visied': 'normal'" v-text="race.has_joined==true? '': ''" class="base-btn-widget" @click="join_race"></span>&nbsp;&nbsp;&nbsp;&nbsp;
                            <span v-for="participant of race.participant">
                                <head-widget :uname="participant.name" :color="participant.self"></head-widget>
                            </span>
                            <div class="pad-h-1"></div>
                        </div>
                    </div>
                    <div class="pad-h-1"></div>
                </div>
                <div class="pad-h-5"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";
    import Head from "@/components/widget/head-widget";

    export default {
        name: "team_race_mode",
        data() {
            var aside_nav_data = [
                {
                    name: "",
                    type: "route",
                    link: "/arena_ranking_list",
                },
                {
                    name: "",
                    type: "route",
                    link: "/arena_checkpoint_mode",
                },
                {
                    name: "",
                    type: "route",
                    link: "/arena_battle_mode",
                },
                {
                    name: "",
                    act: "act",
                    type: "route",
                    link: "/arena_team_race_mode",
                }
            ], team_races = [//data:
                {
                    team_race_id: "team_race_1",
                    title: "",
                    desc: "111Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate.",
                    deadline: "2018.6.1  2018.6.20",
                    member_limit: 5,
                    conscription: ["", "UI"],
                    has_joined: true,
                    participant: [
                        {
                            uid: "u1",
                            name: "",
                        },
                        {
                            uid: "u2",
                            name: "",
                        },
                        {
                            uid: "u3",
                            name: "",
                        },
                        {
                            uid: "u4",
                            self: true,
                            name: "",
                        },
                    ]
                },
                {
                    team_race_id: "team_race_2",
                    title: "",
                    desc: "222Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate.",
                    deadline: "2018.6.1  2018.6.20",
                    member_limit: 7,
                    conscription: ["PHP", "web"],
                    has_joined: false,
                    participant: [
                        {
                            uid: "u1",
                            name: "",
                        },
                        {
                            uid: "u2",
                            name: "",
                        },
                    ]
                },
            ];

            /**/
            team_races.forEach(function (race) {
                race.conscription = race.conscription.join(", ");
            });

            return {

                /*aside_nav_widget*/
                aside_nav_data,

                /**/
                team_races,

            }
        },
        methods: {

            /**/
            join_race(e) {
                if (e.currentTarget.getAttribute("state") === "normal") {//
                    let user = this.$store.state.user;
                    console.log(e.currentTarget.id);//????
                    this.team_races.forEach(function(race){
                        if(race.team_race_id === e.currentTarget.id){
                            race.has_joined = true;
                            race.participant.push({//???
                                uid: user.uid,
                                name: user.uname,
                                self: true,
                            });
                        }
                    })
                }
            }

        },
        components: {
            "aside-nav-widget": AsideNav,
            "head-widget": Head,
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/aside-nav-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-btn-widget.css";
</style>


