<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget"></h2>
                    <hr class="seg-widget"/>

                    <!---->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">

                <!---->
                <div class="pannel-widget pad-w-2">
                    <div class="search-widget">
                        <div class="s-w-inp">
                            <i class="s-w-inp-ico icon-sousuo"></i>
                            <input class="s-w-inp-text" type="text" v-model="search_text"
                                   placeholder="ID ..."/>
                        </div>
                        <span class="s-w-btn" @click="search"></span>
                    </div>
                </div>

                <div class="pad-h-1"></div>

                <div class="pannel-widget">
                    <tab-widget :tabs="tabs"></tab-widget>

                    <!---->
                    <h3 class="base-title-widget bt-w-blue"></h3>
                    <hr class="seg-widget"/>
                    <div class="pad-h-1"></div>
                    <div class="base-table-widget" v-if="challenger.length > 0">
                        <table class="bt-w-table">
                            <thead>
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td"></td>
                                <td class="bt-w-title-td pcenter"></td>
                                <td class="bt-w-title-td pcenter"></td>
                                <td class="bt-w-title-td pcenter"></td>
                            </tr>
                            </thead>
                            <tbody @click="operate_challenge">
                            <tr class="bt-w-tr" v-for="user of challenger">
                                <td class="bt-w-td">
                                    <head-widget :uname="user.name"></head-widget> <span>{{user.name}}</span>
                                </td>
                                <td class="bt-w-td pcenter">{{user.level}} </td>
                                <td class="bt-w-td pcenter">{{user.coin}} </td>
                                <td class="bt-w-td pcenter"><router-link to="/typing" class="bt-w-btn" :challenge-id="user.challenge_id"></router-link> <span
                                        class="bt-w-btn" :void-challenge="user.challenge_id"></span></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span><span class="nr-w-text"></span>
                    </div>
                    <div class="pad-h-2"></div>

                    <!---->
                    <h3 class="base-title-widget bt-w-blue"></h3>
                    <hr class="seg-widget"/>
                    <div class="pad-h-1"></div>
                    <div class="base-table-widget" v-if="fighter.length > 0">
                        <table class="bt-w-table">
                            <thead>
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td"></td>
                                <td class="bt-w-title-td pcenter"></td>
                                <td class="bt-w-title-td pcenter"></td>
                                <td class="bt-w-title-td pcenter"></td>
                            </tr>
                            </thead>
                            <tbody @click="operate_challenge">
                            <tr class="bt-w-tr" v-for="(user, index) of fighter">
                                <td class="bt-w-td">
                                    <head-widget :uname="user.name"></head-widget> <span>{{user.name}}</span>
                                </td>
                                <td class="bt-w-td pcenter">{{user.level}} </td>
                                <td class="bt-w-td pcenter">{{user.coin}} </td>
                                <td class="bt-w-td pcenter"><router-link to="/typing" class="bt-w-btn" :challenge-id="user.fight_id"></router-link></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span><span class="nr-w-text"></span>
                    </div>

                </div>
                <div class="pad-h-5"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";
    import Tab from "@/components/widget/tab-widget";
    import HeadWidget from "@/components/widget/head-widget";

    export default {
        name: "battle_mode",
        data() {
            var vue = this,
                aside_nav_data = [
                {
                    name: "",
                    type: "route",
                    link: "/arena_ranking_list",
                },
                {
                    name: "",
                    type: "route",
                    link: "/arena_checkpoint_mode",
                },
                {
                    name: "",
                    act: "act",
                    type: "route",
                    link: "/arena_battle_mode",
                },
                {
                    name: "",
                    type: "route",
                    link: "/arena_team_race_mode",
                }
            ], tabs = [
                {
                    name: "",
                    state: "act",
                    callback() {
                        vue.challenger = challenger;//????-challenger&fighter
                        vue.fighter = fighter;
                    }
                },
                {
                    name: "",
                    state: "",
                    callback() {
                        vue.challenger = [];//????-challenger&fighter
                    }
                },
                {
                    name: "",
                    state: "",
                    callback() {
                        vue.fighter = [];//????-challenger&fighter
                    }
                },
            ], challenger = [//data:
                {
                    challenge_id: "challenge_1",
                    uid: "u3",
                    name: "",
                    level: "3",
                    coin: "120",
                }
            ], fighter = [//data:
                {
                    fight_id: "fight_1",
                    uid: "u4",
                    name: "",
                    level: "13",
                    coin: "110",
                },
                {
                    fight_id: "fight_2",
                    uid: "u5",
                    name: "",
                    level: "6",
                    coin: "80",
                },
                {
                    fight_id: "fight_3",
                    uid: "u6",
                    name: "",
                    level: "31",
                    coin: "642",
                },
                {
                    fight_id: "fight_4",
                    uid: "u7",
                    name: "",
                    level: "19",
                    coin: "171",
                },
            ];

            return {

                /*aside_nav_widget*/
                aside_nav_data,

                /*tab_widget*/
                tabs,

                /*id*/
                search_text: "",

                /**/
                challenger,

                /**/
                fighter,

            }
        },
        methods: {

            /*ID-????*/
            search() {
            },

            /*-*/
            operate_challenge(e) {
                let tag = e.target,
                    challenge_id,
                    challenger = this.challenger;

                /**/
                if (challenge_id = tag.getAttribute("void-challenge")) {
                    challenger.forEach(function (challenge, index) {
                        if (challenge.challenge_id === challenge_id) {
                            challenger.splice(index, 1);
                        }
                    })
                    //????-challenge_id
                }

                /**/
                if (challenge_id = tag.getAttribute("challenge-id")) {
                    console.log(challenge_id);
                    //????
                }

            }

        },
        components: {
            "head-widget": HeadWidget,
            "tab-widget": Tab,
            "aside-nav-widget": AsideNav,
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/aside-nav-widget.css";
    @import "../../assets/css/base-table-widget.css";
    @import "../../assets/css/main-title-widget.css";
    @import "../../assets/css/search-widget.css";
    @import "../../assets/css/no-result-widget.css";
</style>


