<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget"></h2>
                    <hr class="seg-widget"/>

                    <!---->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <div class="pad-h-1"></div>
                    <div class="pad-left-2">
                        <span class="base-btn-widget" @click="operate_edit"
                              :state="edit_btn.state">{{edit_btn.text}}</span>
                    </div>
                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue"></h2>
                    <div class="grid">
                        <div class="col-2 pad-left-2">
                            <img class="round-img" :src="user_info.cover"/>
                        </div>
                        <div class="col-8">
                            <div class="list-text-widget grid pad-w-2" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.uname">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.sex">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.age">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.tel">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.email">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.addr">
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.jobs">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-2 pcenter pad-right-2">
                            <!--<img :src="'../assets/user/' + user_info.uid + '/erweima.jpg'"/><br/><a></a>-->
                        </div>
                    </div>
                    <hr class="seg-widget"/>
                    <div class="pad-h-2"></div>

                    <h2 class="base-title-widget bt-w-blue"></h2>
                    <div class="pad-w-2">
                        <div class="edit-widget" :state="edit_state" v-for="edu of user_info.edu">
                            <div class="e-w-top"><span class="e-w-del icon-shanchu"
                                                       @click="del_item('edu_' + edu.id)"></span></div>
                            <div class="list-text-widget grid" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.start_time">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.end_time">
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="lt-w-item">
                                        <span>/</span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.chool">
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.major">
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.diploma">
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <textarea class="lt-w-item-textarea" v-model="edu.desc"></textarea>
                                    </div>
                                </div>
                            </div>
                            <div class="e-w-bottom"><span class="e-w-add icon-tianjia" @click="add_item('edu')"></span>
                            </div>
                        </div>
                    </div>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue"></h2>
                    <div class="pad-w-2">
                        <div class="edit-widget" :state="edit_state" v-for="skill of user_info.skill">
                            <div class="e-w-top"><span class="e-w-del icon-shanchu"
                                                       @click="del_item('skill_' + skill.id)"></span></div>
                            <div class="list-text-widget grid" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="skill.name">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <span class="lt-w-item-after">
                                            <select v-model="skill.degree" class="lt-w-item-after-select">
                                                <option value="know"></option>
                                                <option value="shuxi"></option>
                                                <option value="skilled"></option>
                                                <option value="master"></option>
                                            </select>
                                        </span>
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <textarea class="lt-w-item-textarea" v-model="skill.desc"></textarea>
                                    </div>
                                </div>
                            </div>
                            <div class="e-w-bottom"><span class="e-w-add icon-tianjia"
                                                          @click="add_item('skill')"></span></div>
                        </div>
                    </div>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue"></h2>
                    <div class="pad-w-2">
                        <div class="edit-widget" :state="edit_state" v-for="pro of user_info.pro">
                            <div class="e-w-top"><span class="e-w-del icon-shanchu"
                                                       @click="del_item('pro_' + pro.id)"></span></div>
                            <div class="list-text-widget grid" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="pro.name">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="pro.date_time">
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <input type="text" class="lt-w-item-after" v-model="pro.link"
                                               v-if="edit_state == 'edit'">
                                        <a class="lt-w-item-after" :href="pro.link" v-else-if="pro.link">{{pro.link}}
                                            ()</a>
                                        <span class="lt-w-item-after" v-else>{{pro.link}}</span>
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span></span>
                                        <textarea class="lt-w-item-textarea" v-model="pro.desc"></textarea>
                                    </div>
                                </div>
                            </div>
                            <div class="e-w-bottom"><span class="e-w-add icon-tianjia" @click="add_item('pro')"></span>
                            </div>
                        </div>
                    </div>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue"></h2>
                    <div class="pad-w-2">
                        <div class="list-text-widget" :state="edit_state">
                            <div class="lt-w-item">
                                <span></span>
                                <textarea class="lt-w-item-textarea" v-model="user_info.desc"></textarea>
                            </div>
                        </div>
                    </div>

                    <div class="pad-h-2"></div>
                    <div class="pcenter"><span class="base-btn-widget" @click="save_user"></span></div>
                    <div class="pad-h-2"></div>

                </div>
                <div class="pad-h-4"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    export default {
        name: "user_home_resume",
        data() {
            var user_info = {//
                uid: "",
                cover: "",
                uname: "",
                sex: "",
                age: "",
                tel: "",
                email: "",
                addr: "",
                jobs: "",
                desc: "",
                edu: [
                    {
                        id: "",
                        start_time: "",
                        end_time: "",
                        chool: "",
                        major: "",
                        diploma: "",
                        desc: ""
                    }
                ],
                skill: [
                    {
                        id: "",
                        name: "",
                        degree: "",
                        desc: ""
                    },
                ],
                pro: [
                    {
                        id: "",
                        name: "",
                        link: "",
                        date_time: "",
                        desc: ""
                    }
                ]
            };

            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "",
                        type: "route",
                        link: "/user_home",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/user_home_work",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/user_home_blog",
                    },
                    {
                        name: "",
                        type: "route",
                        act: "act",
                        link: "/user_home_resume",
                    },
                    {
                        name: "",
                    },
                ],

                /**/
                user_info,

                /**/
                edit_btn: {
                    text: "",
                    state: "normal"
                },

                /**/
                edit_state: "normal",

            }
        },
        methods: {

            /**/
            operate_edit() {
                if (this.edit_btn.text === "") {
                    this.edit_state = "edit";
                    this.edit_btn.text = "";
                    this.edit_btn.state = "visied";
                } else {
                    this.edit_state = "normal";
                    this.edit_btn.text = "";
                    this.edit_btn.state = "normal";
                }
            },

            /**/
            del_item(id) {
                let id_arr = id.split("_"),
                    record = this.user_info[id_arr[0]],
                    _id = id_arr[1];
                record.forEach((el, i) => {
                    if (el.id == _id) {
                        if (record.length == 1) {//
                            for (let pro in record[0]) {
                                if (pro != "id") record[0][pro] = "";
                            }
                        } else {//
                            record.splice(i, 1);
                        }
                        return;
                    }
                })
            },

            /**/
            add_item(type) {
                let temp = this.user_info[type][0],
                    new_data = {};
                for (let pro in temp) {
                    new_data[pro] = "";
                }
                new_data.id = type + "_" + Date.now();
                this.user_info[type].push(new_data);
            },

            /**/
            save_user() {
                let u = this.user_info;
                this.$axios.put("userinfo/" + this.$store.state.user.uid +"/", {//
                    name: u.uname,
                    age: u.age,
                    gender:	u.sex,
                    address: u.addr,
                    mobile: u.tel,
                    email: u.email,
                    expected_work: u.jobs,
                    assessment: u.desc,
                }).then( res => {
                    if(res.statusText === "OK"){
                        this.$store.commit("update_uname", u.uname);
                    }
                })
            },

        },
        components: {
            "aside-nav-widget": AsideNav,
        },
        mounted() {
            this.$axios.all([
                this.$axios.get("skillvi/"),
                this.$axios.get("education/"),
                this.$axios.get("project/"),
                this.$axios.get("userinfo/"),
            ]).then(this.$axios.spread((r1, r2, r3, r4) => {

                /**/
                let skill_data = r1.data;
                if (skill_data.length > 0) {
                    this.user_info.skill = this.$B.data_convert(skill_data, {
                        id: "1",
                        name: "skill_name",
                        degree: "skill_level",
                        desc: "skill_introduce"
                    })
                }

                /**/
                let edu_data = r2.data;
                if (edu_data.length > 0) {
                    this.user_info.edu = this.$B.data_convert(edu_data, {
                        id: "1",
                        start_time: "Study_time",
                        end_time: "end_time",
                        chool: "school_name",
                        major: "1",
                        diploma: "education",
                        desc: "honor"
                    })
                }

                /**/
                let pro_data = r3.data;
                if (pro_data.length > 0) {
                    this.user_info.pro = this.$B.data_convert(pro_data, {
                        id: "1",
                        name: "project_name",
                        link: "technology",
                        date_time: "time_slot",
                        desc: "project_function"
                    })
                }

                /**/
                let user_data = r4.data[0];
                this.user_info.uid = user_data.id;
                this.user_info.cover = user_data.img;
                this.user_info.uname = user_data.name;
                this.user_info.sex = user_data.gender;
                this.user_info.age = user_data.age;
                this.user_info.tel = user_data.mobile;
                this.user_info.email = user_data.email;
                this.user_info.addr = user_data.address;
                this.user_info.jobs = user_data.expected_work;
                this.user_info.desc = user_data.assessment;

            }));
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/list-text-widget.css";
    @import "../../assets/css/edit-widget.css";
</style>


