// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import App from './App'
import router from './router'
import store from './vuex/store'
import btx from './lib/Btx'

//axios.defaults.baseURL = "http://100ping.com/";//
axios.defaults.baseURL = "http://47.92.92.207/api/";//

Vue.config.productionTip = false
Vue.prototype.$axios = axios;
Vue.prototype.$B = btx;

/**/
var login_url = "http://47.92.92.207/login/",
    relogin = function(){
        axios.post(login_url, {
            username: store.state.user.name,
            password: store.state.user.pwd,
        }).then(res => {
            let token = "JWT " + res.data.token;
            axios.defaults.headers.common['Authorization'] = token;
            store.commit("record_token", token);
            router.replace({//
                path: '/user_home',
            })
        })
    };

Vue.prototype.$utils = {

    /**/
    login_url,

    /**/
    resource: {},

    /**/
    get_res(name, obj){
        if(this.resource[name]){//
            obj[name] = this.resource[name];
        }else{
            axios.get("resource/")
                .then( res => {
                    res.data.forEach( r => {
                        if(r.tag === name){
                            this.resource[name] = obj[name] = r;//
                            return
                        }
                    })
                })
        }
    },

    /**/
    gen_date_time(){
        let now = new Date();
        return now.toLocaleDateString().replace(/\//g, "-") + "T" + now.toLocaleTimeString().split("")[1];
    }

};

if(store.state.user.uid){//
    relogin();
}

/*http request */
axios.interceptors.request.use(
    config => {
        if (store.state.token) {//tokenhttp headertoken
            config.headers.Authorization = store.state.token;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

/*http response */
axios.interceptors.response.use(
    undefined,
    error => {
        let res = error.response;
        switch (res.status) {
            case 401:
                console.warn("token !");
                relogin();
                break;
            case 403:
                console.log('');
                break;
            case 500:
                console.log('');
        }
        return Promise.reject(error.response.data);//
    });

/**/
router.beforeEach((to, from, next) => {
    if (to.meta.check_login == true) {//
        if (store.state.user.uid) {//
            next();
        } else {//

            /**/
            if(to.path === "/learn_start"){
                store.commit("record_cur_route", "/user_home");
            }else{
                store.commit("record_cur_route", to.path);
            }

            next({path: "/login"});
        }
    } else {//
        next();
    }
})

new Vue({
    el: '#app',
    router,
    store,
    components: {App},
    template: '<App/>'
})



